import request from '../assets/request';

class LoginForm extends React.Component {
    constructor (props) {
        super(props)
        this.state = {
            username: "",
            password: "",
            role: "STU"
        }
    }
    render() {
        return (
            <div className="wrapper">
            <div className="form-signin">       
              <h2 className="form-signin-heading">Vui lòng đăng nhập</h2>
              <input type="text" className="form-control" name="username" placeholder="Tên đăng nhập" required="" autoFocus="" onChange={e => this.setState({username: e.target.value})}/>
              <input type="password" className="form-control" name="password" placeholder="Mật khẩu" required="" onChange={e => this.setState({password: e.target.value})}/>   
              <select className="form-control select" name="role" defaultValue onChange={e => this.setState({role: e.target.value})}>
                <option value="STU">Sinh viên</option>
                <option value="LEC">Gỉang viên</option>
                <option value="MAN">Quản lý</option>
              </select>   
              <button className="btn btn-lg btn-primary btn-block" type="submit" onClick={e => this.onSubmit()}>Đăng nhập</button>   
            </div>
          </div>
        );
    }

    onSubmit = () => {
        console.log("ok 0", this.state)
        request('/auth/login', 'POST', '', {"Content-type": "application/json"}, {username: this.username, password: this.password, userRole: this.role})
            .then(result => {
                if(result && result.httpCode && result.httpCode === 200){
                    localStorage.setItem('token', result.result.token)
                    localStorage.setItem('userRole',  this.role);
                    request('/auth/verify', 'GET')
                        .then(result => {
                            localStorage.setItem('userId', result.result.userId);
                            this.props.onSuccess()
                        })
                }
            })
    }
}

export default LoginForm;